html {
    scroll-padding-top: var(--nav-h);
}

:root {
    --green: #2e7f31;
    --blue: #006ac8;

    /* Alternatives:
    --green: #349137;
    --green: #276B29;
    --green: #006137;
    --blue: #02ADEE;
    --blue: #0071b8;
    --blue: #02529C;
    */
    --red: #B41127;
    --gray: #3B3B3B;

    --highlighter-green: #276B29;
    --highlighter-blue: #02529C;
    --highlighter-red: #7B0A0A;

    /* How much left and right padding should the sections have */
    --x-padding: 3rem;
    --nav-h: 72px;
}

h1 {
    color: white;
    font-family: "Inknut Antiqua", serif;
    font-weight: 700;
    font-style: normal;
    line-height: 1.2;
}

p, a {
    color: white;
    font-family: "Work Sans", sans-serif;
    font-optical-sizing: auto;
    font-weight: 400;
    font-style: normal;
}


/* CSS reset
  Browsers apply a bunch of default CSS-styling to HTML elements, some of which
  can be a little annoying from a design perspective. Here we "reset" some of
  those default styling, giving us a more consistent baseline.
*/
body, h1, p {
    margin: 0;
}

* {
    box-sizing: border-box;
}
